@media(max-width: 991px) {
	.footer__wrapp {
		margin-right: 12px;
		flex: 0 1 30%;
		max-width: 30%;
	}
	.footer__wrapp:nth-child(3) {
		flex: 0 1 20%;
	}
	.footer__wrapp:nth-child(4) {
		margin-right: 0;
		flex: 0 1 30%;
	}
	.footer__cabinet {
		margin-right: 0;
		flex: 0 1 21%;
	}
	.footer__subscription {
		flex: 0 1 28%;
		padding: 0;
	}
	.footer__bottom-wrapp {
		justify-content: space-between;
	}
	.footer__bottom-copyright {
		flex: 0 1 38%;
		max-width: 40%;
	}
	.footer__bottom-social {
		flex: 0 1 18%;
		max-width: 18%;
	}
	.footer__bottom-pay {
		flex: 0 1 30%;
		max-width: 34%;
	}
}
@media(max-width: 820px) {
	.footer__main-box {
		flex-direction: column;
	}
	.footer__wrapp {
		position: relative;
		max-width: 100%;
		overflow: hidden;
	}
	.footer__form {
		padding-top: 30px;
	}
	.footer__bottom-wrapp {
		flex-direction: column;
	}
	.footer__content-title {
		max-width: 282px;
	}
	.content__list {
		padding-top: 0;
		height: 0;
		overflow-y: auto;
	}
	.content__item {
		padding-left: 20px;
		padding-bottom: 8px;
	}
	.content__item:last-child {
		padding-bottom: 0;
	}
	.content__title {
		padding: 8px 0;
		position: relative;
		display: flex;
		align-items: center;
		cursor: pointer;
		&::after {
			content: '';
			position: absolute;
			top: 40%;
			right: 10px;
			width: 6px;
			height: 6px;
			background-color: transparent;
			border: 2px solid #fff;
			border-left: none;
			border-top: none;
			transform: translate(0, -60%);
			transform: rotate(-45deg);
		}
	}
	.content__list-active {
		padding-bottom: 10px;
		height: 100%;
	}
	.content__title-active.content__title::after {
		transform: rotate(45deg);
	}
	.footer__bottom-copyright.none {
		display: none;
	}
	.footer__bottom-copyright.block {
		display: block;
		text-align: center;
	}
	.footer__bottom {
		padding-top: 20px;
		display: flex;
    	justify-content: center;
	}
	// *****
	.footer__bottom-copyright {
		max-width: 100%;
	}
	.footer__bottom-social {
		max-width: 100%;
		padding-bottom: 16px;
	}
	.footer__bottom-pay {
		max-width: 100%;
		padding-bottom: 24px;
	}
	// *****
	.footer__bottom-wrapp {
		align-items: center;
	}
	.pay__title h3 {
		text-align: center;
	}
	.social__title h3 {
		text-align: center;
	}
	.social__icon:first-child {
		padding-left: 7px;
	}
	.pay__wrapp {
		height: auto;
	}
}
