.content {
	&__title {
		font-size: 16px;
		font-weight: 500;
		color: #fff;
		text-transform: uppercase;
	}
	&__list {
		padding-top: 20px;
		list-style-type: none;
	}
	&__item {
		padding-bottom: 10px;
	}
	&__item:last-child {
		padding-bottom: 0;
	}
	&__link {
		font-weight: 400;
		font-size: 14px;
		color: #fff;
		text-decoration: none;
		&-transparent {
			color: transparent;
		}
	}
	&__link:hover {
		color: #989696;
		text-decoration: underline;
		transition: color 0.2s;
	}
	&__text {
		display: inline-block;
		font-size: 14px;
		color: #fff;
	}
	&__tel {
		text-decoration: underline;
	}
	&__tel:hover {
		text-decoration: none;
	}
}
.footer__content-title {
	font-size: 16px;
	font-weight: 500;
	color: #fff;
	text-transform: uppercase;
}