.footer__bottom {
	padding-top: 60px;
	&-wrapp {
		display: flex;
	}
	&-copyright {
		flex: 0 1 50%;
		align-self: flex-end;
		font-size: 12px;
		color: #fff;
	}
	&-social {
		flex: 0 1 25%;
	}
	&-pay {
		flex: 0 1 25%;
	}
}

.social {
	&__title {
		h3 {
			font-size: 17px;
			font-weight: 700;
			color: #fff;
			text-transform: uppercase;
		}
	}
	&__wrapp {
		padding-top: 16px;
		display: flex;
	}
	&__icon {
		position: relative;
		padding-right: 28px;
		display: flex;
		flex-direction: column;
		align-items: center;
		img {
			width: 26px;
		}
		span {
			font-size: 10px;
			font-weight: 500;
			color: #fff;
		}
	}
	&__icon:last-child {
		padding-right: 0;
	}
	&__icon:after {
		content: '';
		position: absolute;
		top: 2px;
		right: 11px;
		width: 1px;
		height: 20px;
		background-color: #fff;
	}
	&__icon:last-child:after {
		content: none;
	}
}

.pay {
	&__title {
		h3 {
			font-size: 17px;
			font-weight: 700;
			color: #fff;
			text-transform: uppercase;
		}
	}
	&__wrapp {
		padding-top: 8px;
		display: flex;
		align-items: center;
		height: 59px;
	}
	&__icon {
		padding-right: 16px;
		display: flex;
		flex-direction: column;
		align-items: center;
		img {
			width: 50px;
		}
	}
	&__icon:last-child{
		padding-right: 0;
	}
}