.form {
	padding-top: 30px;
	display: flex;
	flex-direction: column;
	&__input {
		position: relative;
		padding: 10px 0;
		max-width: 100%;
		color: #989696;
		font-size: 12px;
		border: none;
		border-bottom: 1px solid #fff;
		background-color: transparent;
		outline: none;
	}
	&__input:hover {
		border-bottom: 1px solid #989696;
		transition: border-bottom 0.2s;
	}
	&__focus {
		border-bottom: 1px solid #989696;
		transition: border-bottom 0.2s;
	}
	&__input::placeholder {
		color: #989696;
		font-size: 12px;
	}
	&__button {
		margin-top: 30px;
		max-width: 100%;
		height: 50px;
		color: #fff;
		font-size: 16px;
		font-weight: 700;
		background-color: transparent;
		border: 1px solid #fff;
		cursor: pointer;
		outline: none;
	}
	&__button:hover {
		border-color: #989696;
		transition: border-color 0.2s;
	}
	&__button:focus {
		border-color: #989696;
		transition: border-color 0.2s;
	}
	&__checkbox {
		position: relative;
		display: flex;
		margin: 18px 0;
	}
	&__checkbox p {
		padding-left: 20px;
		width: 100%;
		color: #e5e5e5;
		font-size: 10px;
		font-weight: 300;
	}
	&__checkbox a {
		color: #989696;
		text-decoration: underline;
	}
	&__checkbox a:hover {
		color: #e5e5e5;
		text-decoration: none;
		transition: color 0.2s;
	}
	&__list {
		padding-top: 10px;
		list-style-type: none;
	}
	&__link {
		font-size: 14px;
		color: #fff;
		text-decoration: underline;
	}
	&__link:hover {
		color: #989696;
		text-decoration: none;
		transition: color 0.2s;
	}
	&__required {
		color: #e04949;
		font-size: 12px;
		// transform: translate(0, -20px);
		margin-top: -20px;
		transition: 0.2s;
		opacity: 0;
		z-index: -1;
	}
}

.input__checkbox {
	margin-top: 2px;
	position: absolute;
	appearance: none;
	width: 20px;
	height: 20px;
	outline: none;
}

.input__checkbox:focus {
	border-color: #989696;
	transition: border-color 0.4s;
}

.label__checkbox {
	margin-top: 2px;
	position: relative;
	width: 20px;
	height: 20px;
	border: 1px solid #fff;
	cursor: pointer;
	outline: none;
}

.label__checkbox:hover {
	border-color: #989696;
	transition: border-color 0.2s;
}

.label__checkbox:focus {
border-color: #989696;
transition: border-color 0.2s;
}

.label__checkbox:before {
	content: '';
	position: absolute;
	top: 1px;
	left: 1px;
	width: 16px;
	height: 16px;
}

.label__checkbox:after {
	content: '';
	position: absolute;
	top: 1px;
	left: 7px;
	width: 4px;
	height: 11px;
	border: 1px solid #fff;
	border-top: 0;
	border-left: 0;
	opacity: 0;
	transform: rotate(45deg);
	transition: opacity 0.2s;
}

.input__checkbox:checked + .label__checkbox::after {
	opacity: 1;
}

.footer__content-catalog .footer__content-box {
cursor: pointer;
}

input:invalid:not(:placeholder-shown) {
border-color: #e04949;
}

input:valid:not(:placeholder-shown) {
border-color: #989696;
}

input:invalid:not(:placeholder-shown) + .form__required {
opacity: 1;
transition: 0.4s;
margin-top: 5px;
z-index: 1;
}