.footer {
	padding: 50px 0 30px;
	width: 100%;
	height: 100%;
	background-color: #141414;
	font-family: 'Roboto', sans-serif;
	&__main-box {
		display: flex;
	}
	&__wrapp {
		flex: 0 1 25%;
		max-width: 25%;
	}
	&__cabinet {
		display: none;
	}
	&__subscription {
		display: none;
		h3 {
			font-size: 18px;
			font-weight: 500;
			color: #fff;
			text-align: center;
		}
	}
}
.footer__bottom-copyright.block {
	display: none;
}